import React from "react";
import baggageTransfer from "../../assets/baggageTransfer.jpg";
import transferIcon from "../../assets/baggageTransfer.png";
import lostLuggageIcon from "../../assets/lostLuggageIcon.png";
import lostLuggage from "../../assets/lostLuggage.jpg";
import collection from "../../assets/collection.png";
import luggageStorageIcon from "../../assets/luggageStorage.png";
import light from "../../assets/light.png";
import handling from "../../assets/handling.png";
import pickup from "../../assets/pickup.png";
import retrieve from "../../assets/retrieve.png";
import stressFree from "../../assets/stressFree.png";
import yourDoor from "../../assets/yourDoor.png";
import airlines from "../../assets/airlines.png";
import customerApproach from "../../assets/customerApproach.png";
import storageIcon from "../../assets/storageIcon.png";
import drop from "../../assets/drop.png";
import explore from "../../assets/explore.png";
import flexible from "../../assets/flexible.png";
import peace from "../../assets/peace.png";
import "./index.css";

const servicesData = [
  {
    sidebar: {
      icon: transferIcon,
      title: "Baggage Transfer",
      description:
        "The easiest way to handle your luggage needs during travel.",
    },
    main: {
      img: baggageTransfer,
      items: [
        {
          icon: pickup,
          title: "Effortless Pickup",
          description:
            "Upon your arrival, we'll pick up your luggage directly from baggage claim and deliver it to your hotel or residence with efficiency and care. Our dedicated team ensures your belongings are promptly and securely transferred, providing you with convenience right from the start of your journey.",
        },
        {
          icon: collection,
          title: "Convenient Collection",
          description:
            "Before your departure, we'll collect your luggage from your location and ensure it gets to the airport in time for your flight. Our experienced team handles every detail, allowing you to travel worry-free and focus on what matters most.",
        },
        {
          icon: handling,
          title: "Professional Handling",
          description:
            "Your luggage is handled with the utmost care, ensuring it arrives safely and promptly at your destination. We understand the importance of timely and secure baggage transfer, providing you with reliable service every step of the way.",
        },
        {
          icon: light,
          title: "Travel Light",
          description:
            "With our baggage transfer service, you can focus on enjoying your journey while we take care of your luggage. Our dedicated team ensures seamless handling from baggage claim to your doorstep, providing you with peace of mind throughout your travels.",
        },
      ],
    },
  },
  {
    sidebar: {
      icon: lostLuggageIcon,
      title: "Lost Luggage Retrieval",
      description:
        "Effortlessly reclaim your misplaced luggage without the hassle.",
    },
    main: {
      img: lostLuggage,
      items: [
        {
          icon: retrieve,
          title: "Retrieve Your Lost Luggage",
          description:
            "You can request our retrieval service any day of the week. Our professional Valet will coordinate with the airline and retrieve your temporarily lost luggage, delivering it directly to your location.",
        },
        {
          icon: stressFree,
          title: "Seamless and Stress-Free",
          description:
            "Our team handles all the communication and logistics with the airline, ensuring your luggage is retrieved and delivered with care and efficiency.",
        },
        {
          icon: yourDoor,
          title: "Delivered to Your Door",
          description:
            "Your luggage will be delivered to your door, ensuring a convenient and stress-free experience. You can rest easy knowing your belongings are safe and on their way to you.",
        },
        {
          icon: airlines,
          title: "Partnership with Airlines",
          description:
            "Enhancing passenger satisfaction by streamlining lost luggage retrieval.",
        },
        {
          icon: customerApproach,
          title: "Customer-Centric Approach",
          description:
            "By partnering with us, airlines can enhance their customer service experience, providing passengers with a reliable and stress-free solution for lost luggage retrieval.",
        },
      ],
    },
  },
  {
    sidebar: {
      icon: storageIcon,
      title: "Luggage Storage",
      description:
        "Enjoy your layover to the fullest without the burden of luggage.",
    },
    main: {
      img: luggageStorageIcon,
      items: [
        {
          icon: drop,
          title: "Easy Drop-off and Pick-up",
          description:
            "Simply drop off your luggage at our designated location and retrieve it at your convenience. Our service is designed to fit seamlessly into your travel plans.",
        },
        {
          icon: explore,
          title: "Explore with Ease",
          description:
            "With your luggage safely stored, you can enjoy your layover stress-free, knowing your belongings are in good hands.",
        },
        {
          icon: flexible,
          title: "Flexible Options",
          description:
            "Choose from various storage durations to suit your needs. Our facility is equipped to handle luggage of all sizes, ensuring your items are safely stored for as long as you require.",
        },
        {
          icon: peace,
          title: "Peace of Mind",
          description:
            "Our secure storage facility provides you with peace of mind, allowing you to focus on your travels or other commitments without worrying about your belongings.",
        },
      ],
    },
  },
];

const StickySidebar = () => {
  return (
    <>
      {servicesData.map((service, index) => (
        <div key={index} className="w-full flex justify-center mt-28">
          <div className="flex md:justify-between md:flex-row flex-col justify-center lg:w-[85vw] w-[92vw] 2xl:w-[1500px]">
            <div className="sidebar md:w-[33%] w-full md:h-[25vh] md:mb-0 mb-20 md:bg-transparent bg-white md:min-h-[500px] overflow-auto sticky md:top-[16%] top-[1%] md:py-0 py-5">
              <img
                src={service.sidebar.icon}
                alt={service.sidebar.title}
                className="w-20 -rotate-12"
              />
              <h1 className="md:mt-8 text-5xl font-bold">
                {service.sidebar.title}
              </h1>
              <p className="text-[#909399] text-2xl mt-6">
                {service.sidebar.description}
              </p>
            </div>
            <div className="lg:w-[45%] md:w-[60%] w-full flex flex-col">
              <img
                src={service.main.img}
                className="w-full h-[350px] object-cover rounded-2xl"
                alt={service.sidebar.title}
              />
              {service.main.items.map((item, idx) => (
                <div key={idx} className="mt-10 w-full flex lg:gap-8 gap-4">
                  <img className="w-20 h-20" src={item.icon} alt={item.title} />
                  <div className=" w-full">
                    <h1 className="text-3xl font-semibold">{item.title}</h1>
                    <p className="text-xl text-[#909399] mt-3">
                      {item.description}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      ))}
    </>
  );
};

export default StickySidebar;
