import React from "react";
import "./index.css";
import EmblaCarousel from "../EmblaCarousel/EmblaCarousel";
import baggageClaim from "../../assets/logo.png";

function Hero() {

  const OPTIONS = { loop: true };
  const SLIDE_COUNT = 2;
  const SLIDES = Array.from(Array(SLIDE_COUNT).keys());

  return (
    <div className="relative">
      <EmblaCarousel slides={SLIDES} options={OPTIONS} />
      <div className="absolute top-0 left-0 h-screen beforeLg:w-[46%] w-fit beforeLg:ml-20 ml-10 flex items-center">
        <div>
          <img
            src={baggageClaim}
            alt="baggageClaim"
            className="h-20 drop-shadow-2xl z-50 mt-10 -ml-2"
          />
          <p className="text-5xl leading-[55px] !font-thin mb-5 text-with-stroke text-transparent">
            Elevate Your Travel Experience with Baggage Claimed
          </p>
          <p className="text-black text-xl beforeLg:w-[70%] w-[85%]">
            A VIP luggage storage and transfer service that allows travelers to
            skip traditional baggage claim hassles.
          </p>
          <button className="px-8 hover:scale-110 transition-transform duration-500 text-base mt-5 py-[11px] bg-white rounded-full text-black">
            Book Now
          </button>
        </div>
      </div>
    </div>
  );
}

export default Hero;
