import React, { useState } from "react";
import logo from "../../../assets/logo1.png";
import "./index.css";
import { IconButton } from "@mui/material";
import { Close, Menu } from "@mui/icons-material";
import { Link } from "react-router-dom";
import terminalIcon from "../../../assets/hanging/terminal.png";
import arrivalIcon from "../../../assets/hanging/ARRIVAL.png";
import departureIcon from "../../../assets/hanging/DEPARTURE.png";
import contactIcon from "../../../assets/hanging/contact.png";
import lostIcon from "../../../assets/hanging/lost.png";
import baggageIcon from "../../../assets/hanging/BAGGAGE.png";

const Header = ({
  home,
  departure,
  bookNow,
  arrivals,
  contact,
  lost,
  baggage,
}) => {
  const [open, setOpen] = useState(true);
  return (
    <div className="flex justify-between items-center pb-6 lg:px-10 beforeLg:px-5 sm:px-16 px-3 bg-transparent fixed top-0 w-[98.7vw] z-50">
      <Link to={"/"}>
        <img src={logo} alt="logo" className="h-12 drop-shadow-2xl z-50" />
      </Link>

      {/*  <ul
        className={`backdrop-blur-[100px] shadow-lg beforeLg:py-3 py-20 px-5 beforeLg:rounded-full flex beforeLg:flex-row beforeLg:static absolute top-0 ${
          !open ? "left-0" : "-left-[120vw]"
        } z-40 beforeLg:w-auto w-screen h-screen beforeLg:h-auto flex-col beforeLg:gap-4 gap-10 xl:gap-8 beforeLg:items-center bg-[#ffffff99] beforeLg:text-base text-4xl beforeLg:transition-none transition-all duration-700`}
      >
        <Link to={"/"} className={`${home && "text-[#cf0815]"}`}>Terminal</Link>
        <Link to={"/departures"} className={`${departure && "text-[#cf0815]"}`}>Departures</Link>
        <Link to={"/arrival"} className={`${arrivals && "text-[#cf0815]"}`}>Arrivals</Link>
        <Link to={"/contact"} className={`${contact && "text-[#cf0815]"}`}>Information Desk</Link>
        <Link to={"/lost"} className={`${lost && "text-[#cf0815]"}`}>Lost & Found</Link>
        <Link to={"/baggage"} className={`${baggage && "text-[#cf0815]"}`}>Baggage Track</Link>
        <button className="lg:hidden flex">Book Now</button>
        <IconButton
          onClick={() => setOpen(!open)}
          className="!absolute !top-5 !right-8 beforeLg:!hidden flex"
        >
          <Close fontSize="large" />
        </IconButton>
      </ul> */}

      <div className="flex gap-4 items-center ">
        <Link to={"/"} className={`-mt-7 ${!home && "hanging-image"}`}>
          <img
            src={terminalIcon}
            alt="terminal"
            className="w-36 h-28 object-contain"
          />
        </Link>
        <Link to={"/departures"} className={`-mt-7 ${!departure && "hanging-image"}`}>
          <img
            src={departureIcon}
            alt="terminal"
            className="w-36 h-28 object-contain"
          />
        </Link>
        <Link to={"/arrival"} className={`-mt-7 ${!arrivals && "hanging-image"}`}>
          <img
            src={arrivalIcon}
            alt="terminal"
            className="w-36 h-28 object-contain"
          />
        </Link>
        <Link to={"/contact"} className={`-mt-7 ${!contact && "hanging-image"}`}>
          <img
            src={contactIcon}
            alt="terminal"
            className="w-36 h-28 object-contain"
          />
        </Link>
        <Link to={"/lost"} className={`-mt-7 ${!lost && "hanging-image"}`}>
          <img
            src={lostIcon}
            alt="terminal"
            className="w-36 h-28 object-contain"
          />
        </Link>
        <Link to={"/baggage"} className={`-mt-7 ${!baggage && "hanging-image"}`}>
          <img
            src={baggageIcon}
            alt="terminal"
            className="w-36 h-28 object-contain"
          />
        </Link>
      </div>

      <IconButton
        onClick={() => setOpen(!open)}
        className="beforeLg:!hidden flex !absolute !top-5 !right-8"
      >
        <Menu fontSize="large" />
      </IconButton>

      <Link to={"/booking"}>
        <button
          className={`px-5 py-[11px] hover:scale-110 transition-transform duration-500 bg-white rounded-[50px] hover:rounded-full shadow-2xl lg:flex hidden ${
            bookNow ? "text-[#cf0815]" : "text-black"
          }`}
        >
          Book Now
        </button>
      </Link>
    </div>
  );
};

export default Header;
