import React from "react";
import Marquee from "react-fast-marquee";
import img1 from "../../assets/Marquee/1.jpg";
import img2 from "../../assets/Marquee/2.jpg";
import img3 from "../../assets/Marquee/3.jpg";
import img4 from "../../assets/Marquee/4.jpg";
import img5 from "../../assets/Marquee/5.jpeg";
import img6 from "../../assets/Marquee/6.webp";
import img7 from "../../assets/Marquee/7.jpg";
import img8 from "../../assets/Marquee/8.webp";
import img9 from "../../assets/Marquee/9.jpg";
import img10 from "../../assets/Marquee/10.jpg";
import img11 from "../../assets/Marquee/11.jpg";

const ImgMarquee = () => {
  const images = [
    img1,
    img2,
    img3,
    img4,
    img5,
    img6,
    img7,
    img8,
    img9,
    img10,
    img11,
  ];

  const shuffleArray = (array) => {
    let currentIndex = array.length,
      randomIndex;

    while (currentIndex !== 0) {
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex--;

      [array[currentIndex], array[randomIndex]] = [
        array[randomIndex],
        array[currentIndex],
      ];
    }

    return array;
  };

  const shuffledImages1 = shuffleArray([...images]);
  const shuffledImages2 = shuffleArray([...images]);

  return (
    <div className="mt-28 mb-10">
      <Marquee speed={30}>
        {shuffledImages1.map((src, i) => (
          <img
            key={i}
            className="h-[250px] object-contain rounded-3xl mx-2"
            src={src}
            alt=""
          />
        ))}
      </Marquee>
      <Marquee direction="right" speed={30} className="mt-4">
        {shuffledImages2.map((src, i) => (
          <img
            key={i}
            className="h-[250px] object-contain rounded-3xl mx-2"
            src={src}
            alt=""
          />
        ))}
      </Marquee>
    </div>
  );
};

export default ImgMarquee;
