import React from 'react';
import { Facebook, LinkedIn, Twitter, WhatsApp, Email } from '@mui/icons-material';
import logo from "../../../assets/logo1.png"

const Footer = () => {
    return (
        <footer className="bg-black text-white py-12 px-6 lg:px-8 flex justify-center">
            <div className="container w-[90vw] ">
                <div className="flex justify-between gap-8">

                    <div>
                        <h3 className="text-3xl font-semibold mb-4">Our Services</h3>
                        <ul className="space-y-2 text-lg">
                            {["Baggage Tracking", "Book Service", "Luggage Transfer Service", "Lost Luggage Retrieval", "Luggage Storage"].map((p, i) => (
                                <li className='hover:text-[#FFDE59]' key={i}>{p}</li>
                            ))}
                        </ul>
                    </div>

                    <div>
                        <h3 className="text-3xl font-semibold mb-4">Legal</h3>
                        <ul className="space-y-2 text-lg">
                            {["File a Claim", "Become a Partner", "Waiver of Liability", "Insurance Requirement", "Privacy Notice", "Terms & Condition"].map((p, i) => (
                                <li className='hover:text-[#FFDE59]' key={i}>{p}</li>
                            ))}
                        </ul>
                    </div>

                    <div>
                        <h3 className="text-2xl font-semibold mb-4">More</h3>
                        <ul className="space-y-2 text-lg">
                            {["Luggage Storage Near Me", "Luggage Delivery Service", "Luggage Hotel Delivery", "Orlando Luggage Storage", "Orlando Luggage Delivery", "Orlando Airport Luggage Service", "Luggage Transfer Service"].map((p, i) => (

                                <li className='hover:text-[#FFDE59]' key={i}>{p}</li>
                            ))}
                        </ul>
                    </div>
                </div>

                <div className="border-t border-gray-700 mt-8 pt-6 flex flex-col items-center">
                    <img src={logo} className='w-24 mb-2' />
                    <div className="text-sm text-gray-400 mb-2">
                        <span className="hover:text-[#FFDE59]">Privacy Policy</span> |{' '}
                        <span className="hover:text-[#FFDE59]">Terms and Conditions</span> |{' '}
                        <span className="hover:text-[#FFDE59]">Refund Policy</span>
                    </div>

                    <p className="text-lg font-medium text-gray-500">&copy; 2024 BaggageClaimed, LLC. All Rights Reserved.</p>
                </div>

                <div className="mt-8 flex gap-4 justify-center">
                    {[WhatsApp, Email, Facebook, LinkedIn, Twitter].map((Icon, i) => (
                        <span key={i} className="text-white hover:text-[#FFDE59]">
                            <Icon fontSize="large" />
                        </span>
                    ))}
                </div>
            </div>
        </footer>
    );
}

export default Footer;
