import React from 'react';
import BookingForm from '../../Component/BookingForm';
import Header from '../../Component/Layout/Footer/Header';
import "./index.css"

const Booking = () => {
    return <>
    <Header bookNow />

        <div className='flex justify-center overflow-x-hidden relative h-[150vh]'>
            <iframe
            title='map iframe'
                src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d14471.784823221445!2d67.1116591!3d24.8817312!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zMjTCsDUyJzU0LjIiTiA2N8KwMDYnMzkuOSJF!5e0!3m2!1sen!2s!4v1689634005585!5m2!1sen!2s"
                width="600"
                className='w-full h-full'
                height="450"
                style={{ border: 0 }}
                allowFullScreen=""
                loading="lazy"
                referrerPolicy="no-referrer-when-downgrade"
            ></iframe>

            <BookingForm />

        </div>
    </>;
};

export default Booking;