import React from "react";
import Emirates from "../../assets/Airlines/Emirates.png"
import Ethiopian from "../../assets/Airlines/Ethiopian.png"
import Frontier from "../../assets/Airlines/Frontier.png"
import KLM from "../../assets/Airlines/KLM.png"
import Kenya from "../../assets/Airlines/Kenya.png"
import Southwest from "../../assets/Airlines/Southwest.png"
import United_Airlines from "../../assets/Airlines/United_Airlines.png"
import america from "../../assets/Airlines/america.png"
import british from "../../assets/Airlines/british.png"
import delta from "../../assets/Airlines/delta.png"
import qater from "../../assets/Airlines/qatar.png"
import Marquee from "react-fast-marquee";

const AirlineMarquee = () => {
  return (
    <Marquee speed={60}>
      {[Emirates, Ethiopian,, Frontier, KLM, Kenya, Southwest, United_Airlines, america, british, delta, qater].map((src, i) => (
        <img
          key={i}
          className="w-36 aspect-[3/2] object-contain mix-blend-color-burn mx-14"
          src={src}
          alt=""
        />
      ))}
    </Marquee>
  );
};

export default AirlineMarquee;
